#app4 {
  border: 1px solid purple;
}

@keyframes change {
  0% {
    background: red;
  }
  100% {
    background: black;
  }
}
#app4 .circle {
  width: 30vh;
  height: 30vh;
  border-radius: 50%;
  border: 1px solid red;
}
#app4 .circle.active {
  animation: change 1s infinite;
}
